* {
  box-sizing: border-box;
}

h1, h2, h3, a, li, ul, p {
  margin: 0;
  padding: 0;
}

.page {
  scroll-behavior: smooth;
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

.page__body {
  background-color: #f4f4f4;
  max-width: 1280px;
