  text-align: center;
  color: #fff;
  background-color: #333;
  padding: 10px 0;
}

.page__section {
  margin: 20px;
}

.page__section:last-child {
  margin-bottom: 0;
}

.page__section--paper-style {
  background-color: #fff;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 0 5px 1px #0009;
}

.page__section--flex-container {
  flex-wrap: wrap;
  justify-content: space-evenly;
  display: flex;
}

.page__footer {
  color: #fff;
  text-align: center;
  background-color: #333;
  padding: 20px 0;
}

